import Vue from 'vue';
import axios from 'axios';
import router from 'src/router';

// 
if (!!location.port) {
    Vue.prototype.HOST_PROXY_ADMIN = 'http://localhost:8080';  //need change 
} else {
    Vue.prototype.HOST_PROXY_ADMIN = 'http://permission-admin.yuanjy.com'; //need change location.origin.replace(/test/, 'admin');
}

// axios
const axiosInstance = axios.create({
    baseURL: Vue.prototype.HOST_PROXY_ADMIN,
    timeout: 25000,
});

// 
axiosInstance.interceptors.request.use(function (config) {
    //  HOST_PROXY_ADMIN 
    if (config.url.includes(Vue.prototype.HOST_PROXY_ADMIN)) {
        config.emulateJSON = true;
        config.withCredentials = true;
        config.headers['X-TOKEN'] = localStorage.getItem('x-token');
        config.headers['Content-Type'] = 'application/json;charset=UTF-8';
    }
    return config;
}, function (error) {
    return Promise.reject(error);
});

// 
axiosInstance.interceptors.response.use(function (response) {
    //   
    if (response.data.code == 101003 || response.data.code == 101013) {
        localStorage.clear();
        router.replace({
            path: '/login',
        });
    }
    // code0
    if (response.data.code > 0) {
        return Promise.reject(response.data.message);
    }
    return response;
}, function (error) {
    let str = ''
    switch (error.response.status) {
        case 400:
            str = ''
            break
        case 401:
            str = ''
            break
        case 403:
            str = ''
            break
        case 404:
            str = ''
            break
        case 405:
            str = ''
            break
        case 408:
            str = ''
            break
        case 500:
            str = ''
            break
        case 501:
            str = ''
            break
        case 502:
            str = ''
            break
        case 503:
            str = ''
            break
        case 504:
            str = ''
            break
        case 505:
            str = 'HTTP'
            break
    }
    return Promise.reject(str);
});

//  API 
const createAPI = (url, method, config = {}) => {
    return axiosInstance({
        url,
        method,
        ...config
    });
};

export default createAPI;



