// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from 'src/App';
import store from 'src/store';
import router from 'src/router';
import api from 'src/api';

import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';

import Viewer from 'viewerjs'; //  -  window 
import 'viewerjs/dist/viewer.min.css';

import 'normalize.css/normalize.css';// normalize.css 
import 'ionicons/dist/css/ionicons.css'; // 

import 'components/Icon-svg/index'; // svg
import 'assets/iconfont/iconfont'; // iconfont https://github.com/PanJiaChen/vue-element-admin/wiki
import errLog from 'store/errLog'; // error log

// 
Vue.use(ElementUI);

// Vue.config.productionTip = false;
Vue.prototype.$api = api;

// 
if (localStorage.getItem('x-token')) {
    // 
    store.dispatch('GenerateRoutes').then(() => {
        router.addRoutes(store.getters.addRouters);
    });

    // 
    store.dispatch('GetUserInfo').catch(error => {
        Vue.prototype.$message.error(error);
    });
}

// 
if (process.env.NODE_ENV === 'production') {
    Vue.config.errorHandler = function (err, vm) {
        console.log(err, window.location.href);
        errLog.pushLog({
            err,
            url: window.location.href,
            vm
        })
    };
}

// 
new Vue({
    el: '#app',
    router,
    store,
    template: '<App/>',
    components: { App }
})



